import React, { useEffect } from 'react'
import { Badge, Button, Card, Col, Container, Form, Row } from 'react-bootstrap'
import qrCode from '../../assets/qrcodes/adiNathMiniQr.png'
import { message } from 'antd'
import axios from 'axios'
import { useSelector } from 'react-redux'

const Index = () => {
  const user = useSelector((state) => state.user.user)
  const [amount, setAmount] = React.useState('')
  const [transactionId, setTransactionId] = React.useState('')
  const [history, setHistory] = React.useState()

  const initiatePayment = async () => {
    console.log('clicked')

    try {
      if (!amount || !transactionId) return message.error('Please fill all the fields')

      const data = {
        userId: user._id,
        amount,
        transactionId,
      }
      const res = await axios.post('/api/v1/user/addMoneyToWallet', data)

      if (res.data.success) {
        message.success('Payment initiated successfully')
        setAmount('')
        setTransactionId('')
      }
    } catch (error) {
      console.log(error)
      message.error('Something went wrong')
    }
  }

  const getHistory = async () => {
    try {
      const data = {
        userId: user._id,
      }
      const res = await axios.post('/api/v1/user/getAddMoneyToWalletHistory', data)
      if (res.data.success) {
        console.log(res.data)
        setHistory(res.data.data)
      }
    } catch (error) {
      console.log(error)
    }
  }

  const formattedDate = (date) => {
    const dateObject = new Date(date)
    const format = `${dateObject.getFullYear()}-${String(dateObject.getMonth() + 1).padStart(
      2,
      '0',
    )}-${String(dateObject.getDate()).padStart(2, '0')} ${String(dateObject.getHours()).padStart(
      2,
      '0',
    )}:${String(dateObject.getMinutes()).padStart(2, '0')}:${String(
      dateObject.getSeconds(),
    ).padStart(2, '0')}`

    return format
  }

  useEffect(() => {
    if(!history)
    {
      getHistory()
    }
  }, [initiatePayment])

  return (
    <Container fluid className="col-12 col-md-12">
      <Card className="mb-3 w-100">
        <Card.Header>Add to wallet</Card.Header>
        <Container className="d-flex col-12">
          <div className="d-flex col-3">
            <img
              src={qrCode}
              style={{
                width: '100%',
                height: '100%',
                objectFit: 'contain',
              }}
              alt="qrcode"
            />
          </div>
          <div className="d-flex flex-column col-6 offset-2">
            <div className="">
              <div className="d-flex align-items-center justify-content-center mt-3">
                <Button
                  type="button"
                  className="myCustomBtn myCustomHover mx-1"
                  onClick={() => setAmount(100)}
                >
                  100
                </Button>
                <Button
                  type="button"
                  className="myCustomBtn myCustomHover mx-1"
                  onClick={() => setAmount(200)}
                >
                  200
                </Button>
                <Button
                  type="button"
                  className="myCustomBtn myCustomHover mx-1"
                  onClick={() => setAmount(500)}
                >
                  500
                </Button>
                <Button
                  type="button"
                  className="myCustomBtn myCustomHover mx-1"
                  onClick={() => setAmount(1000)}
                >
                  1000
                </Button>
              </div>
            </div>
            <div className="mb-3">
              {' '}
              <Form>
                <Row className="mb-3">
                  <Form.Group as={Col} className="col-12" controlId="formName">
                    <Form.Label>
                      <b>Amount </b>
                    </Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Amount"
                      onChange={(e) => {
                        setAmount(e.target.value)
                      }}
                      value={amount}
                      required
                    />
                  </Form.Group>
                </Row>
                <Row className="mb-3">
                  <Form.Group as={Col} className="col-12" controlId="formName">
                    <Form.Label>
                      <b>Transaction ID</b>
                    </Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Transaction ID"
                      onChange={(e) => {
                        setTransactionId(e.target.value)
                      }}
                      value={transactionId}
                      required
                    />
                  </Form.Group>
                </Row>
                <Row>
                  <div className="d-flex justify-content-end">
                    <Button className="myCustomBtn" onClick={() => initiatePayment()}>
                      Add to wallet
                    </Button>
                  </div>
                </Row>
              </Form>
            </div>
          </div>
        </Container>
      </Card>
      <Card className="mb-3 w-100">
      <Card.Header>Add to wallet history</Card.Header>
          <Container fluid>
            <table className="w-100">
              <tr>
                <th className="text-center">SN</th>
                <th className="text-center">Date</th>
                <th className="text-center">Transaction ID</th>
                <th className="text-center">Amount</th>
                <th className="text-center">Status</th>
                <th className="text-center">Error</th>
              </tr>
              {history && history.length > 0 ? (
                history.map((item, index) => (
                  <tr key={index}>
                    <td className="text-center">{index + 1}</td>
                    <td className="text-center">{new Date(item.date).toLocaleDateString()}</td>
                    <td className="text-center">{item.transactionId}</td>
                    <td className="text-center">{item.amount}</td>
                    <td className="text-center">
                      <Badge
                        className={`${
                          item.status === 'pending'
                            ? 'bg-warning'
                            : item.status === 'approved'
                            ? 'bg-success'
                            : 'bg-danger'
                        } text-white`}
                      >
                        {item.status}
                      </Badge>
                    </td>
                    <td className="text-center">{item.error}</td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={5} className="text-center text-danger">
                    <hr className="w-100" />
                    No data found
                  </td>
                </tr>
              )}
            </table>
          </Container>
      </Card>
      
    </Container>
  )
}

export default Index
